const ava = require("ava")

ava.test('decorated',t =>{
    let f = new Feature({id:2,lat:12.123,lng:23.123})
    
    var feature_in_box = function(feature, box_feature){
        return feature.lat>box_feature.box[0].lat
    }
    
    
    
    
    let feature_list = [
        {
        id:1,
        box:[{
                lat:11.0,
                lng:11.2
            },
            {
                lat:11.0,
                lng:20.0
            },
            {
                lat:20.0,
                lng:20.0
            },
            {
                lat:20.0,
                lng:11.2
            }   
        ]
        },
        {
         id:2,
          box:[{
                lat:30.0,
                lng:1.2
            },
            {
                lat:0.0,
                lng:10.0
            },
            {
                lat:10.0,
                lng:10.0
            },
            {
                lat:10.0,
                lng:1.2
            }   
        ]
        }
    ]
    
    
    
    f.decorate_feature('boxy_id',feature_in_box,feature_list)
    t.true(f.hasOwnProperty('boxy_id'))
})